import {
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  CardHeader,
  TextField,
  Typography,
} from "@mui/material";
import React, { useState } from "react";
import { useCookies } from "react-cookie";
import axios from "axios";
import useStore from "../../../stores/useStore";
import { PostApi } from "../../../apis";

function SignIn({ setAuthView }) {
  const [userEmail, setUserEmail] = useState("");
  const [userPassword, setUserPassword] = useState("");
  const [cookies, setCookies] = useCookies();
  const { user, setUser } = useStore();

  const signInHandler = async () => {
    if (userEmail.length === 0 || userPassword.length === 0) {
      alert("  .");
      return;
    }

    const data = {
      userEmail,
      userPassword,
    };

    const signInResponse = await PostApi("api/auth/signIn", data);
    if (!signInResponse) {
      alert(" ..");
      return;
    }
    if (!signInResponse.result) {
      alert(" .");
      return;
    }

    const { token, exprTime, user } = signInResponse.data;
    const expires = new Date();

    expires.setMilliseconds(expires.getMilliseconds() + exprTime);
    setCookies("token", token, { expires });
    setUser(user);
  };

  return (
    <>
      <Card sx={{ minWidth: 275, maxWidth: "50vw", padding: 5 }}>
        <Box>
          <Typography variant="h5"></Typography>
        </Box>

        <Box height={"50vh"}>
          <TextField
            fullWidth
            label=""
            type="email"
            variant="standard"
            onChange={(e) => setUserEmail(e.target.value)}
          />
          <TextField
            fullWidth
            label=""
            type="password"
            variant="standard"
            onChange={(e) => setUserPassword(e.target.value)}
          />
        </Box>

        <Box component="div">
          <Button fullWidth variant="contained" onClick={() => signInHandler()}>
            
          </Button>
        </Box>
        <Box component="div" display="flex" mt={2}>
          <Typography>  ?</Typography>
          <Typography fontWeight={800} ml={1} onClick={() => setAuthView(true)}>
            
          </Typography>
        </Box>
      </Card>
    </>
  );
}

export default SignIn;
