import { useState } from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Card from "@mui/material/Card";
import { Typography } from "@mui/material";
import { PostApi } from "../../../apis";

const SignUp = ({ setAuthView }) => {
  const [userEmail, setUserEmail] = useState("");
  const [userPassword, setUserPassword] = useState("");
  const [userPasswordCheck, setUserPasswordCheck] = useState("");
  const [userNickname, setUserNickname] = useState("");
  const [userPhoneNumber, setUserPhoneNumber] = useState("");
  const [userAddress, setUserAddress] = useState("");
  const [userAddressDetail, setUserAddressDetail] = useState("");
  const [requestResult, setRequestResult] = useState("");

  const signUpHandler = async () => {
    const data = {
      userEmail,
      userPassword,
      userPasswordCheck,
      userNickname,
      userPhoneNumber,
      userAddress,
      userAddressDetail,
    };

    const signUpResponse = await PostApi("api/auth/signUp", data);
    if (!signUpResponse) {
      alert(" .");
      return;
    }
    if (!signUpResponse.result) {
      alert(" .");
      return;
    }
    alert(" .");
    setAuthView(false);
  };

  return (
    <Card sx={{ minWidth: 275, maxWidth: "50vw", padding: 5 }}>
      <Box>
        <Typography variant="h5"></Typography>
      </Box>
      <Box height={"50vh"}>
        <TextField
          fullWidth
          label=""
          type="email"
          variant="standard"
          onChange={(e) => setUserEmail(e.target.value)}
        />
        <TextField
          fullWidth
          label=""
          type="password"
          variant="standard"
          onChange={(e) => setUserPassword(e.target.value)}
        />
        <TextField
          fullWidth
          label=" "
          type="password"
          variant="standard"
          onChange={(e) => setUserPasswordCheck(e.target.value)}
        />
        <TextField
          fullWidth
          label=""
          variant="standard"
          onChange={(e) => setUserNickname(e.target.value)}
        />
        <TextField
          fullWidth
          label=""
          variant="standard"
          onChange={(e) => setUserPhoneNumber(e.target.value)}
        />
        <TextField
          fullWidth
          label=""
          variant="standard"
          onChange={(e) => setUserAddress(e.target.value)}
        />
        <TextField
          fullWidth
          label=""
          variant="standard"
          onChange={(e) => {
            setUserAddressDetail(e.target.value);
          }}
        />
        <h3>{requestResult}</h3>
      </Box>

      <Box component="div">
        <Button fullWidth variant="contained" onClick={() => signUpHandler()}>
          
        </Button>
      </Box>
      <Box component="div" display="flex" mt={2}>
        <Typography>  ?</Typography>
        <Typography fontWeight={800} ml={1} onClick={() => setAuthView(false)}>
          
        </Typography>
      </Box>
    </Card>
  );
};

export default SignUp;
